  letter-spacing: 0.05em;
}
/* catalog__pagination */
.catalog__pagination-wrapper {
  display: grid;
  grid-template-columns: 120px auto 120px;
  justify-content: space-between;
  font-family: var(--ff-gilroy);
  font-weight: 800;
