  background-color: var(--basic-grey-lighter);
  text-transform: uppercase;
  align-items: center;
}
.catalog__pagination-button {
  display: inline-block;
  padding: 25px 0;
  width: 120px;
  text-align: center;
}
.catalog__pagination-button:hover,
.catalog__pagination-button:active {
  background-color: var(--basic-grey-active);
}
.catalog__pagination-button:active {
  color: var(--basic-black-extra-transparent);
}
.catalog__pagination-list {
  display: grid;
  grid-auto-flow: column;
  column-gap: 30px;
  color: var(--basic-black-extra-transparent);
}
.catalog__page-number--current {
