  position: relative;
  min-height: 580px;
}
.about-us::before,
.contacts::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 80px;
  height: 7px;
  background-color: black;
}
.info__heading {
  font-family: var(--ff-gilroy);
  font-size: 47px;
  font-weight: 800;
  letter-spacing: 0.2em;
