}
.info__map {
  display: block;
}
.footer__logo {
  grid-area: footer-logo;
  margin-top: 18px;
}
.footer__logo:hover {
  opacity: 0.6;
}
.footer__logo:active {
  opacity: 0.3;
}
.footer__user-nav {
  justify-self: end;
  grid-area: footer-user-nav;
  display: grid;
  grid-auto-flow: column;
  justify-content: center;
  gap: 0 40px;
}
.footer__user-nav-link {
  display: inline-block;
  position: relative;
  padding-left: 26px;
