}
.footer__user-nav-link:hover {
  color: white;
}
.footer__user-nav-link:hover::before {
  opacity: 0.6;
}
.footer__user-nav-link:active {
  opacity: 0.3;
}
.footer__user-nav-link::before {
  content: "";
  position: absolute;
  left: 0;
}
.footer__user-nav-link--log-in::before {
