  width: 13px;
  height: 14px;
  bottom: 2px;
}
.footer__user-nav-link--compare::before {
  background-image: url(../img/icon-chart.svg);
  width: 14px;
  height: 14px;
  bottom: 2px;
}
.footer__user-nav-link--cart::before {
  background-image: url(../img/icon-cart.svg);
  width: 10px;
  height: 10px;
  bottom: 4px;
}
.footer__address {
  grid-area: footer-address;
  margin-top: 40px;
}
.footer__nav {
  grid-area: footer-nav;
  margin-top: 40px;
}
.footer__site-nav {
  display: grid;
  grid-auto-flow: column;
  column-gap: 36px;
  justify-content: center;
  font-family: var(--ff-gilroy);
  font-size: 18px;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 0.2em;
}
.footer__site-nav-link:hover {
  opacity: 0.6;
}
.footer__site-nav-link:active {
  opacity: 0.3;
}
.footer__phone-number {
  margin-top: 40px;
  grid-area: footer-phone-number;
  justify-self: end;
}
.footer__social {
  grid-area: footer-social;
  margin-top: 67px;
}
.footer__dev-link {
  justify-self: end;
  grid-area: footer-dev-link;
  margin-top: 67px;
}
.footer__dev-link:hover {
  opacity: 0.6;
}
.footer__dev-link:active {
  opacity: 0.3;
}
.footer__wrapper {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  grid-template-areas: "footer-logo . footer-user-nav" "footer-address footer-nav footer-phone-number" ". footer-social footer-dev-link";
}
.social__list {
  display: grid;
  grid-auto-flow: column;
  justify-content: center;
